.nav {


  &__list {
    display: flex;
    gap: 34px;
  }

  &__link {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    @include adaptiv-value("font-size", 18, 15, 0);
    line-height: 25px;
    color: var(--light);
    transition: 0.5s;

    &:hover {
      color: var(--orange);
    }
  }
}
