.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 71px 0 15px 0;

  @include tablet {
    display: none;
  }

  &__container {
    display: flex;
    align-items: center;
  }

  &__logo {
    font-weight: 800;
    font-style: normal;
    text-transform: uppercase;
    color: var(--light);
    @include adaptiv-value("font-size", 21, 19, 0);
    max-width: 65px;
    width: 100%;
    transition: 0.5s;

    &:hover {
      color: var(--orange);
    }
  }

  &__row {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 65px;
  }

  &__btn {
    display: flex;
    gap: 26px;
  }

  &__sign-in {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    @include adaptiv-value("font-size", 18, 14, 0);
    line-height: 25px;
    color: var(--light);
    background-color: transparent;
    border-radius: 5px;
    padding: 16px 43px;
    transition: 0.5s;

    &:hover {
      background-color: var(--orange);
    }
  }

  &__sign-up {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 500;
    @include adaptiv-value("font-size", 18, 14, 0);
    line-height: 25px;
    color: var(--light);
    background-color: var(--orange);
    border-radius: 5px;
    padding: 16px 43px;
    transition: 0.5s;
  }
}
